<template>
  <div>
    <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>{{$route.query.name}} </h1>
      <div class="chartWrap">
        <router-link :to="{path:'/search',query: {}}">
          <span class="search iconfont"> &#xe625; </span>
        </router-link>
        <span class="bag"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a class="iconfont cart"  @click="$router.push('cart')"> &#xe669; </a></div>
    </section>
      <section class="salesWrap">
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle" > Best sales of this Category </h3>
          </div>
          <div>
            <a href="javascript:void(0)" @click="$router.push({path:'searchResult',query:{category1Code:categoryOneCode,orderByVolume:'DESC'}})" class="blue"> See All</a>
          </div>
        </div>
        <div class="commonPro">
          <swiper :options="commonSwiperOption">
            <template v-for="(item,index) in categoryTwo.cateSpuVo ">
              <swiper-slide>
                <div class="item">
                  <div class="tips_cutOff" v-if="item.cutOff"> {{item.percentage}} OFF </div>
                  <div class="tips_love iconfont" :class="{'active':item.love}"> {{item.love?"&#xe653;":"&#xe652;"}} </div>
                  <div class="tips_sale" v-if="item.sale"> </div>
                  <img class="img" :src="item.spuMainImage" alt="">
                </div>
                <div class="name"> {{item.name}} </div>
                <div class="bottomPrice">
                  <p class="red"> {{item.priceRange}} AED  </p>
                  <p class="gray"> {{item.priceRangeLine}} AED </p>
                  <p v-if="item.isTypeFour==1"> Free Shipping </p>
                </div>
                <div class="downShare">
                  <div :class="{'active':item.share}"> <span class="share iconfont"> &#xe650; </span> </div>
                  <div :class="{'active':item.cart}"> <span class="cart iconfont"> &#xe669; </span> </div>
                </div>
              </swiper-slide>
            </template>
          </swiper>
        </div>

        <div class="box mt10 box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle" > {{$route.query.name}}  </h3>
          </div>
          <div>
            <a href="javascript:void(0)" class="blue" @click="$router.push({path:'searchResult',query:{category1Code:categoryOneCode}})"> See All</a>
          </div>
        </div>
        <div class="categrorySales">
            <ul>
              <li v-for="(item,index) in categoryTwo.cateTwoVo" :key="index" @click="$router.push({path:'searchResult',query:{category2Code:item.categoryCode}})">
                <div class="item">
                  <img class="img" :src="item.img" alt="">
                  <div class="bottomName">
                    {{item.categoryName}}
                  </div>
                </div>
              </li>
            </ul>
        </div>

      </section>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
  name: 'home',
  data () {
    return {
      show:false,
      categoryOneCode:this.$route.query.categoryCode,
      commonSwiperOption:{
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
      addChartVisible:false,
      timer:{
        "d":"00",
        "h":"00",
        "m":"00",
        "s":"00"
      },
      proDataConsumerElectronics:[
        {
          "cutOff":"",
          "love":1,
          "img":"static/images/consumerElectronicsPro1.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          "free":0,
          "cart":0,
          "share":0,
          "sale":0
        },
        {
          "cutOff":"",
          "love":0,
          "img":"static/images/consumerElectronicsPro2.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          "free":0,
          "cart":0,
          "share":0,
          "sale":0
        },
        {
          "cutOff":"",
          "love":1,
          "img":"static/images/consumerElectronicsPro3.png",
          "name":"Name of the store and  shopeone Name of the store and  shopeone ",
          "price":341.22,
          'hisPrice':682.44,
          "free":1,
          "cart":0,
          "share":0,
          "sale":0
        }
      ],
      flashData:[
        {
          "img":"static/images/consumerElectronicsPro3.png",
          "name":"Protective shell"
        },
        {
          "img":"static/images/consumerElectronicsPro1.png",
          "name":"Protective shell"
        },
        {
          "img":"static/images/consumerElectronicsPro2.png",
          "name":"Protective shell"
        },
        {
          "img":"static/images/consumerElectronicsPro2.png",
          "name":"Protective shell"
        }
      ]
    }
  },
  computed: {
    ...mapGetters(['categoryTwo','indexData'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        this.$store.dispatch('getCategoryTwo',{parentCategoryCode:this.categoryOneCode}).then(res=>{})
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
  @import "../../common/css/category.scss";
</style>



